import React, { Component } from 'react'
import List4G from './List4G'

class DownApp extends Component {
    constructor(){
        super();
        this.state = {
            sitesSemouha : [],
            Down4G : [],
            Down3G : [],
            Down2G : []
        }
    }

    componentDidMount(){
        fetch('DataJson/sitesSemouha.json')
        .then((responder) => responder.json())
        .then((data) => {
            this.setState({sitesSemouha: data.data})
        })
        fetch('DataJson/Down4GFiltered.json')
        .then((responder) => responder.json())
        .then((data) => {
            this.setState({Down4G: data.data})
        })
    }

    render(){
        const {Down4G,sitesSemouha} = this.state;
        return(
            <div className='tc'>
                <List4G Data4G={Down4G}/>
            </div>
        )
    }
}

export default DownApp;