import React from 'react'
import {Button,Popover} from 'antd'

const MindCard = ({SiteName,IPAddress,Netmask,GWaddress,NMSport,NEport}) => {
    const CardContent = (
        <div>
            <p>IP Address : {IPAddress} </p>
            <p>Netmask : {Netmask} </p>
            <p> GW Address : {GWaddress}</p>
            <p> NMS Port : {NMSport} </p>
            <p> NE Port : {NEport}</p>
        </div>
    )
    return (
        <Popover
        content={SiteName} title={CardContent}>
            <Button type="primary" className='tc dib br3 pa3 ma2 bw2 shadow-5'>{SiteName}</Button>
        </Popover>
    )
}

export default MindCard;