import React from 'react';
import SiteCard from './SiteCard';

const SiteList = ({sites}) => {
    const SiteCompnent = sites.map((user , i) => {
        return (
            <SiteCard 
                key={i}
                ShortCode={sites[i].ShortCode}
                SiteNameTakenFromSiteCount={sites[i].SiteNameTakenFromSiteCount}
                NewOffice={sites[i].NEWOFFICE}
                CoLocatedSiteCode={sites[i].CoLocatedSiteCode}
                CoLocatedSiteName={sites[i].CoLocatedSiteName}
                BSC_RNC={sites[i].BSCRNC}
                LongCode={sites[i].LongCode}
                Zone={sites[i].Zone}
                SiteType={sites[i].SiteType}
                GeneratorType={sites[i].GeneratorType}
            />
        )
    })
    return (
        <div>
            {SiteCompnent}
        </div>
    )
}

export default SiteList;