import React from 'react';
import Tilt from 'react-tilt';

const logo = () => {
    return (
        <div className='ma4 mt0'>
            <Tilt className="Tilt" options={{ max : 35 }} style={{ height: 30, width: 400 }} >
                <div className="Tilt-inner">
                    <h1 className="pointer f2">Semouha office</h1>
                </div>
            </Tilt>
        </div>
    )
}

export default logo;