import React, {Component} from 'react';
//import {sites} from '../../public/DataJson/SitesData.json';
//import {sites} from '../DataJson/SitesData.json'
import SiteList from './SiteList'
import SearchBox from '../SearchBox'


class SiteSearchApp extends Component {

    constructor(){
        super();
        this.state = {
            sites: [],
            SearchFeild: '',
        }
    }

    componentDidMount(){
        fetch('DataJson/sites.json')
        .then((responder) => responder.json())
        .then((data) => {
            this.setState({sites:data.data})
        })
        //this.setState({sites:sites})
    }

    onSearchChange = (event) => {
        this.setState({SearchFeild: event.target.value})
    }
    

    render(){
        const {sites,SearchFeild} = this.state;
        const FilteredSites = sites.filter(site => {
            return (site.ShortCode.toLowerCase().includes(SearchFeild.toLowerCase()) 
            || site.SiteNameTakenFromSiteCount.toLowerCase().includes(SearchFeild.toLowerCase())
            || site.CoLocatedSiteCode.toLowerCase().includes(SearchFeild.toLowerCase())
            || site.CoLocatedSiteName.toLowerCase().includes(SearchFeild.toLowerCase())
            );
        })
        return(
    
            <div className='tc'>
                <SearchBox SearchChange={this.onSearchChange} />
                <SiteList sites={FilteredSites}/>
            </div>
            
        )
    }
}

export default SiteSearchApp;