import React from 'react';
import MindCard from './MindCard';

const MindList = ({DataDCN}) => {
    const MindComponent = DataDCN.map((user , i) => {
        return (
            <MindCard
                key={i}
                SiteName={DataDCN[i].SiteName}
                IPAddress={DataDCN[i].IPAddress}
                Netmask={DataDCN[i].Netmask}
                GWaddress={DataDCN[i].GWaddress}
                NMSport={DataDCN[i].NMSport}
                NEport={DataDCN[i].NEport}
            />
        )
    })
    return (
        <div>
           {MindComponent} 
        </div>
    )
}

export default MindList;