import React , {Component} from 'react'
//import {data as Awayed} from '../DataJson/AwayedDCN.json'
//import {data as Mansheya} from '../DataJson/MansheyaDCN.json'
//import {data as SMOUHA} from '../DataJson/SMOUHADCN.json'
import SearchBox from '../SearchBox'
import MindList from './MindList';

class MindDCNApp extends Component {
    constructor(){
        console.log('constructor')
        super();
        this.state = {
            Awayed: [],
            Mansheya: [],
            SMOUHA: [],
            SearchFeild: '',
        }
    }

    componentDidMount(){
        console.log("did mount")
        fetch('DataJson/AwayedDCN.json')
        .then((r) => r.json())
        .then((data) =>{
            this.setState({Awayed: data.data})
        })
        fetch('DataJson/MansheyaDCN.json')
        .then((responder) => responder.json())
        .then((data) =>{
            this.setState({Mansheya: data.data})
        })
        fetch('DataJson/SMOUHADCN.json')
        .then((responder) => responder.json())
        .then((data) => {
            this.setState({SMOUHA: data.data})
        })
    }

   onSearchChange = (event) => {
       this.setState({SearchFeild: event.target.value})
    } 

    render(){
        console.log("render")
        const {Awayed,Mansheya,SMOUHA,SearchFeild} = this.state;
        const FilteredAwayed = Awayed.filter(AwayedF => {
            return (
                AwayedF.SiteName.toLowerCase().includes(SearchFeild.toLowerCase())
            );
        })
        const FilteredMansheya = Mansheya.filter(MansheyaF => {
            return (
                MansheyaF.SiteName.toLowerCase().includes(SearchFeild.toLowerCase())
            );
        })
        const FilteredSMOUHA = SMOUHA.filter(SMOUHAF => {
            return (
                SMOUHAF.SiteName.toLowerCase().includes(SearchFeild.toLowerCase())
            );
        })

        return(
            <div className='tc'>
                <SearchBox SearchChange={this.onSearchChange}/>
                <MindList DataDCN={FilteredAwayed}/>
                <MindList DataDCN={FilteredMansheya}/>
                <MindList DataDCN={FilteredSMOUHA}/>
                <br></br><br></br>
            </div>
        )
    }

}

export default MindDCNApp;