import React from 'react';
import SiteSearchApp from '../SearchSite/SiteSearchApp';
import MindDCNApp from '../MindDCN/MindDCNApp'
import UpdateDataBase from '../BackEndComp/UpdateDataBase'
import FilterSitesDown from '../BackEndComp/FilterSitesDown'
import DownApp from '../Down/DownApp'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

  import { Menu, Button } from 'antd';
  import {
    FilterOutlined,
    MenuUnfoldOutlined,
    MenuFoldOutlined,
    LaptopOutlined,
    ApartmentOutlined,
    FileSearchOutlined,
    ContainerOutlined,
    
    MailOutlined,
  } from '@ant-design/icons';
  
  
  class nav2 extends React.Component {
    state = {
      collapsed: false,
    };
  
    toggleCollapsed = () => {
      this.setState({
        collapsed: !this.state.collapsed,
      });
    };
  
    render() {
      return (
        <Router>
          <div>
          <nav>
          <div style={{ width: 256 }}>
          <Button type="primary" onClick={this.toggleCollapsed} style={{ marginBottom: 16 }}>
            {React.createElement(this.state.collapsed ? MenuUnfoldOutlined : MenuFoldOutlined)}
          </Button>
          <Menu
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            mode="inline"
            theme="dark"
            inlineCollapsed={this.state.collapsed}
          >
            
              <Menu.Item key="1" icon={<LaptopOutlined />}>
              <Link to="/">Home</Link>
              </Menu.Item>
            
            
              <Menu.Item key="2" icon={<FileSearchOutlined />}>
              <Link to="/SiteSearchApp">Search Site</Link>
              </Menu.Item>
            
            
            <Menu.Item key="3" icon={<ApartmentOutlined />}>
            <Link to="/MindDCNApp">Mind DCN </Link>
            </Menu.Item>
           
            
              <Menu.Item key="4" icon={<FilterOutlined />}>
              <Link to="/FilterSitesDown"> Filter Down</Link>
              </Menu.Item>
            
          </Menu>
          
          <Switch>
                <Route path="/SiteSearchApp">
                    <SiteSearchApp />
                </Route>
                <Route path="/MindDCNApp">
                    <MindDCNApp />
                </Route>
                <Route path="/FilterSitesDown">
                    <UpdateDataBase />
                </Route>
            </Switch>
        </div>
        </nav>
        </div>
        </Router>
      );
    }
  }
export default nav2;