import './css/App.css';
import React, {Component} from 'react'
import SiteSearchApp from './SearchSite/SiteSearchApp';
import MindDCNApp from './MindDCN/MindDCNApp'
import UpdateDataBase from './BackEndComp/UpdateDataBase'
import FilterSitesDown from './BackEndComp/FilterSitesDown'



class App extends Component{

  constructor(){
    super();
    this.state = {
      Pages: 0
    }
  }

  FnPages = (value) => () => {
    this.setState({Pages : value})
  }

  render(){
    //const {Pages,FnPages} = this.state;
    if(this.state.Pages === 0){
      return (
        <div className='tc' id="root">
          <h1 className='f1'>Semouha office</h1>
          <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(1)}>Site Search</button>
          <br></br><br></br>
          <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(2)}>MIND DCN</button>
          <br></br><br></br>
          <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(3)}>Filter Sites Down</button>
          <br></br><br></br>
          <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(4)}>Update DataBase</button>
        </div>
        
      )
    }
    else if(this.state.Pages === 1){
      console.log(this.Pages)
      return (
        <div className='tc'>
        <h1 className='f1'>Semouha office</h1>
        <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(0)}>Go Back</button>
        <SiteSearchApp />
        </div>
      )
    }
    else if(this.state.Pages === 2){
      return(
        <div className='tc'>
        <h1 className='f1'>Semouha office</h1>
        <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(0)}>Go Back</button>
        <br></br><br></br>
        <MindDCNApp />
        </div>
      )
    }
    else if(this.state.Pages === 2){
      return(
        <div className='tc'>
        <h1 className='f1'>Semouha office</h1>
        <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(0)}>Go Back</button>
        <br></br><br></br>
        <MindDCNApp />
        </div>
      )
    }
    else if(this.state.Pages === 3){
      return(
        <div className='tc'>
        <h1 className='f1'>Semouha office</h1>
        <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(0)}>Go Back</button>
        <br></br><br></br>
        <FilterSitesDown />
        </div>
      )
    }
    else if(this.state.Pages === 4){
      return(
        <div className='tc'>
        <h1 className='f1'>Semouha office</h1>
        <button className='f4 grow no-underline br-pill ph3 pv2 mb2 dib black bg-dark-pink' onClick={this.FnPages(0)}>Go Back</button>
        <br></br><br></br>
        <UpdateDataBase />
        </div>
      )
    }
  }
}

export default App;