import React from 'react';
import {Button,Popover} from 'antd'

const SiteCard = ({ShortCode,SiteNameTakenFromSiteCount,NewOffice,CoLocatedSiteCode,CoLocatedSiteName,BSC_RNC,LongCode,Zone,SiteType,GeneratorType}) => {

    const CardContent = (
        <div>
            <p>Site Name Taken From Site Count : {SiteNameTakenFromSiteCount}</p>
            <h2>{CoLocatedSiteCode}</h2>
            <p>Co-Located Site Code : {CoLocatedSiteCode}</p>
            <p>Co-Located Site Name : {CoLocatedSiteName}</p>
            <p>Office : {NewOffice}</p>
            <p>BSC_RNC : {BSC_RNC}</p>
            <p>LongCode : {LongCode}</p>
            <p>Zone : {Zone}</p>
            <p>SiteType : {SiteType}</p>
            <p>GeneratorType : {GeneratorType}</p>
        </div>
    )

    return (
        <Popover title={ShortCode} content={CardContent}>
            <Button type="primary" className='tc dib br3 pa3 ma2 bw2 shadow-5'>{SiteNameTakenFromSiteCount}_{ShortCode}</Button>
        </Popover>
        // <div className='tc bg-light-green dib br3 pa3 ma2 grow bw2 shadow-5'>
        //     <h2>{ShortCode}</h2>
        //     <p className='b'>SiteNameTakenFromSiteCount</p>
        //     <p>{SiteNameTakenFromSiteCount}</p>
        //     <h2>{CoLocatedSiteCode}</h2>
        //     <p className='b'>CoLocatedSiteCode</p>
        //     <p>{CoLocatedSiteName}</p>
        //     <p className='b'>Office</p>
        //     <p>{NewOffice}</p>
        //     <p className='b'>BSC_RNC</p>
        //     <p>{BSC_RNC}</p>
        //     <p className='b'>LongCode</p>
        //     <p>{LongCode}</p>
        //     <p className='b'>Zone</p>
        //     <p>{Zone}</p>
        //     <p className='b'>SiteType</p>
        //     <p>{SiteType}</p>
        //     <p className='b'>GeneratorType</p>
        //     <p>{GeneratorType}</p>
        // </div>
    )
}

export default SiteCard;