import React from 'react'
import {Button,Popover} from 'antd'
const Card4G = ({Name,AlarmText,AlarmType,DiagnosticInfo,SupplementaryInformation}) => {
    
    const CardContent = (
        <div>
            <p>Alarm Text : {AlarmText}</p>
            <p>Alarm Type : {AlarmType}</p>
            <p>Diagnostic Info : {DiagnosticInfo}</p>
            <p>Supplementary Information : {SupplementaryInformation}</p>
        </div>
    )
    
    return (
        <Popover title={Name} content={CardContent}>
            <Button type="primary" className='tc dib br3 pa3 ma2 bw2 shadow-5'>{Name}</Button>
        </Popover>
    )
}

export default Card4G;