import React from 'react';
import './navigation.css';
import Logo from '../logo/logo';
import SiteSearchApp from '../SearchSite/SiteSearchApp';
import MindDCNApp from '../MindDCN/MindDCNApp'
import UpdateDataBase from '../BackEndComp/UpdateDataBase'
import FilterSitesDown from '../BackEndComp/FilterSitesDown'
import DownApp from '../Down/DownApp'
import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
  } from "react-router-dom";

const navigation = ({buttonValue}) => {
    return (
        <Router>
            <div>
                <nav style={{display:'flex',flexDirection:'row',justifyContent:'space-between'}}>
                    {/* <div>
                        <h1 className='f2 pa2' style={{justifyContent:'flex-start'}}>Semouha office</h1>
                    </div> */}
                    <div>
                        <Logo />
                    </div>
                    <div className='pa3' style={{display:'flex',flexDirection:'row',justifyContent:'flex-end'}}>
                        <Link to="/">
                            <button className='btn-grad pointer'>Home</button>
                        </Link>
                        <Link to="/SiteSearchApp" >
                            <button className='btn-grad pointer' >Site Search</button>
                        </Link>
                        <Link to="/MindDCNApp">
                            <button className='btn-grad pointer' >Mind DCN</button>
                        </Link>
                        {/* <Link to="/FilterSitesDown">
                            <button className='btn-grad pointer' >Filter Down</button>
                        </Link> */}
                    </div>
                </nav>
                <Switch>
                <Route path="/SiteSearchApp">
                    <SiteSearchApp />
                </Route>
                <Route path="/MindDCNApp">
                    <MindDCNApp />
                </Route>
                <Route path="/FilterSitesDown">
                    <UpdateDataBase />
                </Route>
            </Switch>
            </div>
        </Router>
    )
}

export default navigation;