import React from 'react'
import Card4G from './Card4G'

const List4G = ({Data4G}) => {

    const Component4G = Data4G.map((user,i)=>{
        return(
            <Card4G 
                key={i}
                Name={Data4G[i].Name}
                AlarmText={Data4G[i].AlarmText}
                AlarmType={Data4G[i].AlarmType}
                SupplementaryInformation={Data4G[i].SupplementaryInformation}
                DiagnosticInfo={Data4G[i].DiagnosticInfo}
            />
        )
    })

    return(
        <div>
            {Component4G}
        </div>
    )
}

export default List4G;